import "./Navbar.css";
import { Link } from "react-scroll";
import Main from "./nav";

const Navbar = () => {
  return (
    <div id="nav-menu">
      <Main />
      <button type="button" className="mobile-nav-toggle d-lg-none">
        <i className="icofont-navigation-menu"></i>
      </button>
      <header id="header" className="fixed-top">
        <div className="container-fluid  navbur">
          <div className="navi">
            <div className="col-xl-12 d-flex align-items-center lefty">
              <h5 className="ld-flex mr-auto devman">
                <img
                  src="https://code.google.com/images/developers.png"
                  alt=""
                />
                salil-01 <span className="blink"> _</span>
              </h5>
              <nav className="nav-menu  mainMenu">
                <ul>
                  <li>
                    <Link
                      to="home"
                      spy={true}
                      smooth={true}
                      offset={-50}
                      duration={500}
                      activeClass="active"
                    >
                      Home
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="about"
                      spy={true}
                      smooth={true}
                      duration={700}
                      activeClass="active"
                    >
                      About
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="skills"
                      spy={true}
                      smooth={true}
                      offset={-70}
                      duration={800}
                      activeClass="active"
                    >
                      Skills
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="projects"
                      spy={true}
                      smooth={true}
                      duration={900}
                      activeClass="active"
                    >
                      Projects
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="contact"
                      spy={true}
                      smooth={true}
                      duration={1000}
                      activeClass="active"
                    >
                      Contact
                    </Link>
                  </li>
                </ul>
              </nav>
              <div className="nav-social">
                <a
                  href="https://github.com/salil-01"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fa fa-github"></i>
                </a>
              </div>
              <div className="left-btns">
                <div class=" " id="theme-button2">
                  <input id="toggle" class="toggle" type="checkbox"></input>
                </div>
                <div>
                  <div className="CvMe">
                    <a
                      href="https://drive.google.com/file/d/10iArQ7pjCqLluP_LSxb7Qm66Cb44p5OA/view?usp=share_link"
                      download="RESUME.pdf"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <button className="my-cv">
                        <span style={{ marginRight: "5px" }}>Resume</span>
                        <i class="bx bx-download"></i>
                      </button>{" "}
                    </a>
                  </div>
                </div>

                <div className=" " id="theme-button">
                  <Link to="#" class="menuBtn">
                    <span class="lines"></span>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </header>
    </div>
  );
};

export default Navbar;
